body {
    font-family: 'Source Code Pro', monospace;
    padding: 0px;
    color: black;
    font-size: 2vmin;
}

.right {
    float: right;
}

.left {
    float: left;
}

.red {
    background-color: cyan;
}

.green {
    background-color: green;
}

.centered {
    text-align: center;
}


.clickable:hover {
  cursor: pointer;
}

.centerText {
    text-align: center;
}

.white {
    background-color: white;
}

.CenterImg {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
}

.CompScreen {
    width: 100vw;
    height: 100vh;
    background-color: blue;
}

.Icon {
    position: absolute;
    z-index: 0;
    width: 20vh;
    align-items: center
}

.IconIcon {
    width: 50%;
    height: 50%;
    display:block;
    margin:auto;
}

.IconLabel {
    color: white;
    text-align: center;
    text-shadow: .1vw .1vw .3vw black, -.1vw -.1vw .3vw black;
}

.IconLabel:hover {
    color: #36a7d4;
}

.Menu .PopUp {

}

.Menu {
}

.MenuHeader {
    background-color: grey;
    border-style: outset;
    border-width: 2px;
    color: black;
    padding: 1%;
    min-height: 2vw;
}

.MenuLabel {
}

.CancelButton{
    color: black;
}

.CancelButton:hover {
    color: red;
}


.Window {
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    overflow: auto; /* Enable scroll if needed */
    animation-name: menuOpen;
    animation-duration: 0.5s;
    width: 98%;
    height: 90%;
    margin-left: 1%;
    margin-top: 1%;

    border-color: white;
    border-style: inset;
}

.WriteUp {
    margin: auto;
    padding: 5%;
}

.WriteUp img {
    max-width: 90%;
    margin: auto;
    display: block;
}

.WriteUp video {
    max-width: 90%;
    margin: auto;
    display: block;
}

@keyframes menuOpen {
    0% { transform: scale(0); }
    70% { transform: scale(1.02); }
    100% { transform: scale(1);}
}

.LeftSlide {
    margin: 1%;
    padding: 1%;
    background-color: white;
    animation-name: slideLeft;
    animation-duration: .9s;
}

@keyframes slideLeft {
    0% {transform: translate(1000px, 0px) scale(0, 0);}
    50% {transform: translate(2000px, 0px) scale(0, 0);}
    100% {transform: translate(0px, 0px) scale(1, 1);}
}

.RightSlide {
    margin: 1%;
    padding: 1%;
    background-color: white;
    animation-name: slideRight;
    animation-duration: .9s;
}

@keyframes slideRight {
    0% {transform: translate(-1000px, 0px) scale(0, 0);}
    50% {transform: translate(-2000px, 0px) scale(0, 0);}
    100% {transform: translate(0px, 0px) scale(1, 1);}
}


.Banner {
    animation-name: ticker;
    animation-duration: 20s ;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}

@keyframes ticker {
    0% {transform: translate(120%, 0px);}
    100% {transform: translate(-120%, 0px);}
}
